
body {
  font-family: Arial, sans-serif;
  background-color: blue;
  margin: 0;
  padding: 0;
  text-align: center;
}
.app-container {
  margin-top: 50px;
  padding: 20px;
  background-color: aqua;
}
.profile-card {
  border: 2px solid red;
  background-color: green;
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  margin: auto;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
}

.profile-card h3 {
  color: orange;
  font-size: 30px;
}

.profile-card p {
  font-size: 16px;
  background-color:pink;
}
h2{
  color: navy;
}